$color_1: #1EACC7;
$color_2: #FF5656;
$color_3: #fff;

@mixin btn_hover {
  &:hover {
    background-image: none;
    background-color: $color_2; } }

@mixin main_hover {
	&:hover {
		content: '';
		color: $color_2; } }

@mixin sub_wiht {
	background-color: #ffff;
	color: #000; }


* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative;
  height: 50px; }

nav {
  height: 50px;
  background: #000;
  position: fixed;
  z-index: 5;
  width: 100%; }

.container {
  width: 1140px; }

.menu {
  display: flex;
  padding: 13px 70px;
  margin: 0;
  justify-content: space-between;
  list-style-type: none;
  .link {
    font-family: 'RobotoL';
    font-size: 15px;
    line-height: 15px;
    color: #ffff;
    text-align: center;
    list-style-type: none;
    text-decoration: none; }
  &_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  top: 50px;
  background-color: rgba(0, 0, 0, 0.6);
  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }
  &_text {
    font-family: 'RobotoL';
    margin-top: 6px;
    font-size: 13px;
    color: #FFFFFF;
    opacity: 0.5; }
  &_call {
    font-family: 'RobotoL';
    font-size: 12px;
    color: #ffff;
    text-align: end;
    margin-top: 19px; }
  &_phone {
    display: block;
    text-align: end;
    font-size: 22px;
    text-decoration: none;
    color: #ffff; } }
.subheader_but {
	display: block;
	width: 172px;
	height: 46px;
	font-size: 14px;
	margin-top: 20px;
	background-image: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
	box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
	border-radius: 4px;
	border: 0px;
	text-transform: uppercase;
	color: #ffff;
	@include btn_hover; }


.promo {
  min-height: 800px;
  background: url(../img/uber_bg.png) center (center / cover) no-repeat;
  text-align: center;
  padding: 140px 0 135px 0;
  &_header {
    font-weight: 900;
    font-size: 60px;
    color: #FFFFFF;
    text-transform: uppercase; }
  &_subheader {
    font-weight: 900;
    font-size: 35px;
    color: #FFFFFF;
    line-height: 36px;
    margin: 0;
    margin-top: 15px;
    line-height: 35px;
    text-transform: uppercase; }
  &_descr {
    font-size: 17px;
    line-height: 24px;
    color: #FFFFFF;
    margin: 0;
    margin-top: 33px; }
		&_btn {
    width: 172px;
    height: 46px;
    margin-top: 20px;
    background-image: linear-gradient(0deg, $color_1 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: 0px;
    text-transform: uppercase;
    color: #ffff;
    width: 259px;
    height: 63px;
    margin: 0;
    margin-top: 120px;
    font-weight: 18px;
    @include btn_hover; } }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  text-transform: uppercase;
  text-align: center; }

.reasons {
  margin-bottom: 0;
  margin-top: 38px;
  text-align: center;
  font-weight: 900;
  font-size: 38px;
  text-transform: uppercase; }

.reasons_for {
	padding-top: 20px;
	padding-bottom: 65px;
	.reasons_descr {
		display: flex;
		justify-content: center;
		margin: 0 auto;
		margin-top: 35px;
		.reasons_round {
			display: flex;
			justify-content: center;
			margin: 0 auto;
			align-items: center;
			width: 116px;
			height: 116px;
			margin-right: 25px;
			border-radius: 100%;
			background: $color_1; } } }

.reasons_descr_text {
  width: 370px; }

.reasons_descr_text_sec {
    width: 370px; }

.reasons_for .reasons_descr_text .work_reason, .reasons_descr_text_sec .work_reason {
  font-family: 'RobotoL';
  font-size: 20px;
  line-height: 20px;
  color: $color_1; }

.reasons_for .reasons_descr_text .work_descr_mini, .reasons_descr_text_sec .work_descr_mini {
  font-family: 'RobotoL';
  font-size: 15px;
  line-height: 16px; }

.subtitle {
	font-weight: 400;
	font-size: 17px;
	margin-top: 19px;
	text-align: center; }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url(../img/mobile_bg.png) center (center / cover) no-repeat;
	.row {
			margin-top: 32px; }
	&_item {
			position: relative;
			width: 320px;
			height: 280px;
			margin: 0 auto;
			margin-bottom: 19px;
			padding-top: 92px;
			&_subtitle {
					font-size: 24px;
					text-transform: uppercase;
					color: #FFFFFF;
					text-align: center; }
			&_plus {
					display: flex;
					justify-content: center;
					align-items: center;
					position: absolute;
					bottom: 35px;
					left: 49%;
					transform: translate(-50%);
					width: 26px;
					height: 26px;
					background-color: #000000;
					border-radius: 100%;
					margin: 0 auto;
					border-radius: 100%;
					cursor: pointer;
					&:before {
						content: '';
						display: block;
						left: 50%;
						width: 14px;
						height: 2px;
						background-color: $color_1; }
					&:after {
						content: '';
						position: absolute;
						left: 50%;
						transform: translateX(-50%);
						width: 2.2px;
						height: 14px;
						background-color: $color_1; }
					&:hover {
						&:before {
							content: '';
							background-color: red;
							transition: .3s all;
							transform: scale(1.2); }
						&:after {
							content: '';
							background-color: red;
							transition: .3s all;
							transform: scale(1.2);
							left: 49%; } } }
			&_1 {
					background: url(../icons/card1.png) center (center / cover) no-repeat; }
			&_2 {
					background: url(../icons/card2.png) center (center / cover) no-repeat; }
			&_3 {
					background: url(../icons/card3.png) center (center / cover) no-repeat; }
			&_4 {
					background: url(../icons/card4.png) center (center / cover) no-repeat; }
			&_5 {
					background: url(../icons/card5.png) center (center / cover) no-repeat; }
			&_6 {
					background: url(../icons/card6.png) center (center / cover) no-repeat; } } }

.uber_car {
	position: relative;
	background: url(../img/mobile_bg.png) center (center / cover) no-repeat;
	min-height: 895px;
	padding: 55px 0  140px 0;
	z-index: 2;
	.label {
			background-color: #fff;
			color: #000;
			z-index: 2; }
	.reasons {
			color: #fff; }
	h2.subtitle {
			color: #fff;
			margin-top: 0px; }
	.car_bg {
			display: block;
			margin: 0 auto;
			margin-top: 55px; }
	.dark_bg {
			position: absolute;
			background: url(../img/dark_bg.png) center (center / cover) no-repeat;
			height: 500px;
			left: 0px;
			top: 0px;
			width: 100%;
			z-index: -1; }
	.car_info {
			text-align: center;
			font-family: 'RobotoL'; } }

.require {
	padding: 70px 0 95px 0;
	&_block {
		margin: 33px 0;
		&_nbm {
			margin-bottom: 0px; } }
	&_title {
		font-size: 22px;
		line-height: 24px;
		font-weight: 400;
		color: #1CA8C3;
		&_nbm {
			color: $color_2; } }
	&_info {
		font-family: 'RobotoL';
		text-decoration: underline;
		font-size: 17px;
		line-height: 24px;
		color: #1CA8C3; }
	&_list {
		font-family: 'RobotoL';
		font-weight: 300;
		list-style: none;
		margin-top: 20px;
		padding-left: 50px;
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		color: #222222;
		li {
			font-family: 'RobotoL';
			position: relative;
			font-weight: 300;
			line-height: 24px;
			margin-bottom: 10px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../icons/Layercheckblue.png') center (center/cover) no-repeat; } } }
	.warning {
		.require_title {
			color: $color_2; }
		.require_list {
			li {
				&:before {
					background: url('../icons/check_red.png') center (center/cover) no-repeat; } } } } }

.world {
	background: url('../img/world_bg.png') center (center/cover) no-repeat;
	min-height: 720px;
	padding: 50px 0 22px 0;
	position: relative;
	.label {
		@include sub_wiht; }
	.reasons {
		margin-top: 26px;
		color: $color_3;
		font-size: 38px;
		line-height: 48px; }
	.subtitle {
		color: $color_3;
		margin-top: 2px; }
	.world_watch {
		position: absolute;
		left: 50%;
		bottom: 50px;
		transform: translateX(-50%);
		font-size: 15px;
		text-transform: uppercase;
		color: $color_1;
		text-decoration: none;
		@include main_hover; } }


footer {
	padding: 14px 23px 39px 23px; }

.footer {
	&_divider {
		width: 301px;
		height: 4px;
		background-color: $color_1;
		margin: 0 auto;
		margin-top: 12px; }
	&_wrapper {
		display: flex;
		padding-top: 32px;
		justify-content: space-between; }
	&_wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		display: flex;
		justify-content: space-between;
		width: 170px;
		&_items {
			width: 35px;
			height: 35px;
			background-color: #000;
			border-radius: 100%;
			margin: 0 4px;
			display: flex;
			justify-content: center;
			align-items: center;
			text-decoration: none;
			color: #ffff;
			@include main_hover; } }
	&_links {
		width: 390px;
		margin: 0 auto;
		text-transform: uppercase;
		text-align: center;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				font-family: 'RobotoL';
				text-decoration: none;
				font-size: 15px;
				color: #070716; } }
		&_search {
			display: flex;
			justify-content: space-between;
			margin-top: 15px;
			a {
				font-family: 'RobotoL';
				text-decoration: none;
				font-size: 11px;
				color: #999999; } }
		&_lang {
			display: block;
			margin-top: 20px;
			font-size: 11px;
			color: $color_1;
			text-decoration: none;
			@include main_hover; }
		&_sub {
			margin-top: 5px;
			img {
				vertical-align: middle; } } } }



